<template>
    <section class="container section-margin">

        <div class="subscribe-section">

            <h2 class="section-heading">
                Подпишитесь на рассылку
            </h2>

            <form class="subscribe-form">

                <label class="form-control" for="main-subscribe-name">
                    <input class="form-control__input" id="main-subscribe-name"
                           type="text"
                           @blur="$v.userName.$touch()"
                           v-model="userName">
                    <span class="form-control__label">Имя</span>
                </label>

                <label class="form-control" for="main-subscribe-email">
                    <input class="form-control__input"
                           id="main-subscribe-email"
                           type="email"
                           placeholder="example@mail.com"
                           @blur="$v.userEmail.$touch()"
                           v-model="userEmail">
                    <span class="form-control__label">
<!--                    Электронная почта-->
                </span>
                </label>

                <label class="form-control form-control_wide" for="main-subscribe-comment">
                <textarea class="form-control__input form-control__input_textarea"
                          id="main-subscribe-comment"
                          placeholder="Комментарий"
                          @blur="$v.userComment.$touch()"
                          v-model="userComment">
                </textarea>
                    <span class="form-control__label">
<!--                    Комментарий-->
                </span>
                </label>

                <button class="submit-button">Подписаться</button>

            </form>

        </div>

    </section>
</template>

<script>
    import {email, minLength, required} from 'vuelidate/lib/validators';

    export default {
        name: 'SubscribeForm',
        data() {
            return {
                userEmail: '',
                userName: '',
                userComment: ''
            }
        },
        validations: {
            userEmail: {
                required,
                email
            },
            userName: {
                required
            },
            userComment: {
                required
            }
        }
    }
</script>

<style lang="scss" scoped>

    @import "../root";

    .subscribe-section {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border: 1px solid #14a5da;
        padding: 42px 36px 0 36px;

        &::after {
            content: '';
            position: absolute;
            width: 14px;
            height: 14px;
            top: 6px;
            left: 6px;
            background-size: 14px;
            background-image: url('../assets/img/RSS.svg');
            background-repeat: no-repeat;

            @media (min-width: $media-sm-resolution) {
                width: 21px;
                height: 21px;
                top: 14px;
                left: 15px;
                background-size: 21px;
            }

        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;

            border: 24px solid transparent;
            border-left: 24px solid #e3f4fb;
            border-top: 24px solid #e3f4fb;

            @media (min-width: $media-sm-resolution) {
                border: 44px solid transparent;
                border-left: 44px solid #e3f4fb;
                border-top: 44px solid #e3f4fb;
            }
        }
    }

    .subscribe-section .section-heading {
        font-size: 12px;
        font-weight: 600;
        text-align: center;
        margin-bottom: 24px;

        @media (min-width: $media-xs-resolution) {
            font-size: 16px;
        }

        @media (min-width: $media-sm-resolution) {
            margin-bottom: 28px;
            font-size: 20px;
        }

    }

    .subscribe-form {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        max-width: 470px;
        justify-content: space-between;

        @media (min-width: $media-xs-resolution) {
            max-width: 500px;
        }

        @media (min-width: $media-sm-resolution) {
            max-width: 570px;
        }

    }

</style>